Vue.component('vue-trade-menu', {
  props: [],
  template: `
  <div class="left-nav">
    <div class="trade-nav" style="overflow: visible;">
      <div v-if="3 != org_info_theme && false" class="trade-nav__market" @click="chgCurMarket('marketA')" v-bind:class="{'active': 'marketA' == curMarket}">A</div>
      <div v-if="3 != org_info_theme && false" class="trade-nav__market" @click="chgCurMarket('marketH')" v-bind:class="{'active': 'marketH' == curMarket}" style="margin-right: 55px;">H</div>

      <span class="trade-nav__item single-buy" v-bind:class="{'active': 'single-buy' == curActive}" @click="chgActive('single-buy')"></span>
      <span class="trade-nav__item single-sell" v-bind:class="{'active': 'single-sell' == curActive}" @click="chgActive('single-sell')"></span>

      <div style="flex: 1;"></div>
      <a class="trade-nav__toggle" @click="chgShowBoard()">
        <i class="nav-bar-show-icon" v-bind:class="{'icon-collapse': showBoard, 'icon-spread': !showBoard}"></i>
      </a>
    </div>
  </div>
  `,
  data: function(){
    return {
      org_info_theme: window.LOGIN_INFO.org_info.theme,
      curMarket: 'marketA',
      curActive: 'single-buy',
      displayRevertNum: 0,
      showBoard: true,
      product_list: [],
      checked_count: 0
    }
  },
  watch: {
    checked_count: function(){
      if (1 == this.checked_count) {
        $('.disabled-mask').hide();
      }else{
        $('.disabled-mask').show();
      }
    },
  },
  methods: {
    chgShowBoard: function(){
      this.showBoard = !this.showBoard;
      this.$emit('menu', {
        curActive: this.curActive,
        curMarket: this.curMarket,
        showBoard: this.showBoard
      });
    },
    chgCurMarket: function(curMarket){
      // 
      if(curMarket !== this.curMarket){
        $('#stock_code').val('').change();
        $("#val_price").val('');

        $('[click-active=".trade_number_method-by_volume"]').click()
        $('#val_volume').val('');
        if ('marketA' == curMarket) {
          $('.order_model_limit_price').click();
          $("#val_volume").attr('placeholder', '100');
          $("#vale_current_volum").attr('placeholder', '100');
        }else if ('marketH' == curMarket) {
          $("#val_volume").attr('placeholder', '');
          $("#vale_current_volum").attr('placeholder', '');
        }
      }

      this.curMarket = curMarket;
      this.showBoard = true;

      this.$emit('menu', {
        curActive: this.curActive,
        curMarket: this.curMarket,
        showBoard: this.showBoard
      });
    },
    chgActive: function(val){
      if (1 != this.checked_count && ('multi-buy' == val || 'multi-sell' == val)) {
        $.omsAlert('',false,1000);
        return;
      }
      this.curActive = val;
      this.showBoard = true;

      var index = 0;
      if ('single-sell' == this.curActive) {
        index = 1;
      }
      $.omsUpdateLocalJsonData('etc','order_create_nav_index', index);

      this.$emit('menu', {
        curActive: this.curActive,
        curMarket: this.curMarket,
        showBoard: this.showBoard
      });

      tradeFlow.chgCurActive(this.curActive);
    }
  }
});

