Vue.component('vue-trade-form', {
  props: ['cur_active'],
  template:`
  <form class="vue-trade-form">
    <div class="vue-trade-form__line" v-bind:class="'single-buy' == cur_active ? 'buy-color-cls' : 'sell-color-cls'">
      <label class="vue-trade-form__label"></label>
      <div class="vue-trade-form__content">
        <vue-stock-search assets_class="e,f" :custom_cls="'trade-custom-tmp-class'" :stock_input_id="stock_input_type_id" :placeholder="''" v-on:stock_id="chgStockId($event)" v-on:stock_input="stock_input_type_id = $event"></vue-stock-search>
      </div>
    </div>
    <div class="vue-trade-form__line" v-bind:class="'single-buy' == cur_active ? 'buy-color-cls' : 'sell-color-cls'">
      <label class="vue-trade-form__label"></label>
      <div class="vue-trade-form__content">
        <div class="vue-trade-form__content--radio">
          <label @click="chgTradeMode(1)" class="vue-trade-form__content--radio-label" v-bind:class="{'active': 1 == trade_model}">
            
            <i class="right-icon"></i>
          </label>
          <label style="width: 10px;"></label>
          <label @click="chgTradeMode(2)" class="vue-trade-form__content--radio-label" v-bind:class="{'active': 2 == trade_model}">
            
            <i class="right-icon"></i>
          </label>
        </div>
      </div>
    </div>
    <div class="vue-trade-form__line" v-bind:class="'single-buy' == cur_active ? 'buy-color-cls' : 'sell-color-cls'">
      <label class="vue-trade-form__label"></label>
      <div class="vue-trade-form__content">
        <input v-if="1 == trade_model" v-model="trade_price" @change="chgTradePrice()" class="vue-trade-form__content--input" placeholder="" />
        <input v-if="2 == trade_model" disabled="disabled" :value="''" @change="chgTradePrice()" class="vue-trade-form__content--input" placeholder="" />
      </div>
    </div>
    <div class="vue-trade-form__line" v-bind:class="'single-buy' == cur_active ? 'buy-color-cls' : 'sell-color-cls'">
      <label class="vue-trade-form__label"></label>
      <div class="vue-trade-form__content" style="display: flex;">
        <select class="vue-trade-form__content--select" v-model="add_ins_method" @change="chgInsMethod()" :disabled="lockStatus">
          <option value="3"></option>
          <option value="1"></option>
          <option value="2"></option>
        </select>
        <a v-if="false" class="icon-lock" :class="[{'unlocked' : !lockStatus}, {'locked' : lockStatus}]" style="margin-right: 15px;" @click="chgLockStatus()"><i></i></a>
      </div>
    </div>
  </form>
  `,
  data: function(){
    return {
      stock_id: '',
      stock_input_type_id: '',
      lockStatus: false,
      trade_model: 1,
      trade_price: '',
      add_ins_method: 3, // 1 2 3
    }
  },
  mounted: function(){
    let _this = this;
    // 
    $(window).on('trade-5__priceClick', (event) => {
      this.trade_price = event.price;
      this.chgTradePrice();
    });

    $(window).on('trade-5__firstTime_price', event => {
      if ('single-buy' == this.cur_active) {
        this.trade_price = event.ask1_price;
      }else if('single-sell' == this.cur_active){
        this.trade_price = event.bid1_price;
      }
      this.chgTradePrice();
    });

    //  
    common_storage.getItem('lock__ins_trade_number_method' + window.LOGIN_INFO.user_id, function(rtn){
      if (0 == rtn.code) {
        if ('true' == rtn.data) {
          _this.lockStatus = true;
          common_storage.getItem('lock__ins_trade_number_method__value' + window.LOGIN_INFO.user_id, function(rtn){
            if (0 == rtn.code) {
              _this.add_ins_method = rtn.data;
            }
          })

          // $('.icon-lock').addClass('locked');
          // $('.trade_number_method').attr('disabled', true);
          // common_storage.getItem('lock__ins_trade_number_method__value' + window.LOGIN_INFO.user_id, function(rtn){
          //   if (0 == rtn.code) {
          //     $('.trade_number_method').val(rtn.data).change();
          //   }
          // })
        }else{
          _this.lockStatus = false;
          // $('.icon-lock').addClass('unlocked');
          // $('.trade_number_method').attr('disabled', false);
        }
      }else{
        $('.icon-lock').addClass('unlocked');
      }
    });
  },
  methods: {
    chgLockStatus: function(){
      let dstValue = !this.lockStatus;
      if (true == dstValue) {
        common_storage.setItem('lock__ins_trade_number_method' + window.LOGIN_INFO.user_id, 'true');
        common_storage.setItem('lock__ins_trade_number_method__value' + window.LOGIN_INFO.user_id, this.add_ins_method);
      }else{
        common_storage.setItem('lock__ins_trade_number_method' + window.LOGIN_INFO.user_id, 'false');
        
      }
      this.lockStatus = dstValue;
    },
    chgStockId: function(event){
      this.stock_id = event;
      this.$emit('stock_id', this.stock_id);
      tradeFlow.chgStockId(this.stock_id);
    },
    chgTradeMode: function(val){
      this.trade_model = val;
      tradeFlow.chgTradeMode(this.trade_model);
    },
    chgTradePrice: function(){
      tradeFlow.chgTradePrice(this.trade_price);
    },
    chgInsMethod: function(){
      tradeFlow.chgInsMethod(this.add_ins_method);
    }
  }
});

